.district-overview .text {
  font-size: 0.7vw;
  line-height: 0.7vw;
  text-align: left;
  color: #064562;
  margin-bottom: 0.2vw;
}

.district-overview .flex-row {
  display: flex;
}

//BP那个输入名字的框
.district-overview .BPBName {
  margin: 0;
  padding: 0;

  & .MuiInputBase-input {
    padding: 0;
    width: 9.5vw;
    max-height: 1.25vw;
    overflow: visible;
    color: white;
    font-size: 1.25vw;
